@import '../../styles/mixins/sizing';

@mixin cards-layout() {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;

	>* {
		margin: 1.5% 3%;
	}

	@include on-mobile {
		margin: 0;

		>* {
			margin: 0;
		}
	}
}

.cards-area {
	@include cards-layout();
}
